import React, { Component } from 'react';
import octogoin from '../images/octogoin.jpg';

class Projects extends Component {
    render() {
        return (
            <section className="section" id="projects">
                <div className="container">
                    <div className="columns">
                        <div className="column">
                            <h1 className="title is-3 is-underlined">Projects</h1>
                        </div>
                    </div>
                    <div className="tile is-ancestor">
                        <div className="tile is-vertical is-8">
                            <div className="tile">
                                <div className="tile is-parent is-vertical">
                                    <article className="tile is-child notification is-dark">
                                        <a href="https://github.com/ryanimpey/Cat-Facts-Twitter-Bot-Lincoln-Hack" className="subtitle is-roboto-light">Cat Facts</a>
                                        <p>
                                            A small Twitter bot created for the Lincoln Hack hackathon. Including #catfact
                                            in your tweet would notify our bot to respond with an image of a cat, as well
                                            as an interesting cat fact!
                                        </p>
                                    </article>
                                    <article className="tile is-child notification is-light">
                                        <a href="https://github.com/ryanimpey/Chrome-MC-Status/" className="subtitle is-roboto-light">Chrome MC Status</a>
                                        <p>
                                            A vanillaJS chrome extension to check the current status of Minecraft servers, built rapidly to learn how
                                        chrome extensions work.
                                    </p>
                                    </article>
                                </div>
                                <div className="tile is-parent">
                                    <article className="tile is-child notification is-info">
                                        <p className="title">Associate Developer</p>
                                        <p className="subtitle">University of Lincoln</p>
                                        <p>
                                            Part of the development team for a project soon to be announced using
                                            ASP.net, and Javascript/JQuery, as well as libraries such as LeafletJS.

                                            More info available after release.
                                        </p>
                                    </article>
                                </div>
                            </div>
                            <div className="tile is-parent">
                                <article className="tile is-child notification is-dark">
                                    <div className="content">
                                        <a className="title" href="https://github.com/ryanimpey/University-Of-Lincoln">University of Lincoln</a>
                                        <p>A repo to store all assignment work during the Computer Science BSc.</p>
                                        <p>Using languages C#, C++, and frontend development languages/markup</p>
                                    </div>
                                </article>
                            </div>
                        </div>
                        <div className="tile is-parent">
                            <article className="tile is-child notification is-light">
                                <div className="content">
                                    <p className="title">Octocoin</p>
                                    <p>
                                        NodeJS Reward Site allowing users to complete surveys and install apps,
                                        rewarded with virtual currency which could be redeemed at the onsite shop,
                                        with automated item delivery to the players linked Steam account via automated bots.
                                        Using a technology stack of NodeJS, Express, Mongo, and Jade (templating). Over 8,000
                                        users registered at closing with over 30,000 completed offers.
                                    </p>
                                    <figure class="image is-256x256">
                                        <img alt="Octocoin logo" src={octogoin} className="is-image-circle"></img>
                                    </figure>
                                </div>
                            </article>
                        </div>
                        </div>
                    </div>
            </section>
                );
    }
}

export default Projects;


