import React, { Component } from 'react';

class Timeline extends Component {
    render() {
        return (
            <section className="hero is-medium is-light" id="experience">
                <div className="hero-body">
                    <div className="container">
                        <h1 className="title is-2 is-roboto-bold">Experience / Education</h1>
                        <div className="columns is-centered">
                            <div className="column is-5">

                                <div className="timeline">
                                    <header className="timeline-header">
                                        <span className="tag is-info">2016</span>
                                    </header>
                                    <div className="timeline-item">
                                        <div className="timeline-marker"></div>
                                        <div className="timeline-content">
                                            <p className="heading">September 2016</p>
                                            <p>A-Levels with 300 UCAS Points</p>
                                            <p>Began studying Computer Science at the</p>
                                            <p>University of Lincoln</p>
                                        </div>
                                    </div>
                                    <div className="timeline-item">
                                        <div className="timeline-marker"></div>
                                        <div className="timeline-content">
                                            <p className="heading">October 2016</p>
                                            <p>Development work for Octogoin website</p>
                                        </div>
                                    </div>
                                    <div className="timeline-item">
                                        <div className="timeline-marker"></div>
                                        <div className="timeline-content">
                                            <p className="heading">December 2016</p>
                                            <p>Began work at the University of Lincoln</p>
                                            <p>as part of the Associate Developer team</p>
                                        </div>
                                    </div>
                                    <header className="timeline-header">
                                        <span className="tag is-info">2017</span>
                                    </header>
                                    <div className="timeline-item">
                                        <div className="timeline-marker is-icon">
                                            <i className="fa fa-flag"></i>
                                        </div>
                                        <div className="timeline-content">
                                            <p className="heading">January - December 2017</p>
                                            <p>Development of web applications for the University</p>
                                            <p>of Lincoln to aid student tasks/everyday needs</p>
                                        </div>
                                    </div>
                                    <div className="timeline-item">
                                        <div className="timeline-marker is-icon">
                                            <i className="fa fa-flag"></i>
                                        </div>
                                        <div className="timeline-content">
                                            <p className="heading">September 2017</p>
                                            <p>Began second year of Computer Science BSc</p>
                                        </div>
                                    </div>
                                    <div className="timeline-header">
                                        <span className="tag is-info">2018</span>
                                    </div>
                                    <div className="timeline-item"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </section>
        )
    }
}

export default Timeline;


