import React, { Component } from 'react';

class Header extends Component {
    render(){
        return(
            <nav className="navbar is-fixed-top" aria-label="main-navigation">
                <div className="navbar-brand">
                    <div className="navbar-item">Ryan Impey</div>
                </div>
                <div className="navbar-menu">
                    <div className="navbar-end">
                        <div className="navbar-item"><a href="#about">About</a></div>
                        <div className="navbar-item"><a href="#experience">Experience</a></div>
                        <div className="navbar-item"><a href="#projects">Projects</a></div>
                        <div className="navbar-item"><a href="#skills">Skills</a></div>
                        <div className="navbar-item"><a href="#contact">Contact</a></div>
                    </div>
                </div>
            </nav>
        )
    }
}

export default Header;


