import React, { Component } from 'react';
import { Github, Twitter, Mail } from 'react-feather';

class Contact extends Component {

    constructor(){
        super();

        this.state = {
            currentYear: (new Date()).getFullYear()
        }
    }

    render(){
        return(
            <section className="section" id="contact">
                <div className="columns is-centered">
                    <div className="column is-6">
                        <h1 className="is-roboto-bold title is-3">Contact</h1>
                        <p><a href="https://github.com/ryanimpey" target="_blank" rel="noopener noreferrer"><Github></Github>: RyanImpey</a></p>
                        <p><a href="https://twitter.com/wholelifeisary" target="_blank" rel="noopener noreferrer"><Twitter></Twitter>: WholeLifeIsARy</a></p>
                        <p><a href="mailto:ryanimpey**NOSPAM**@live.co.uk" target="_blank" rel="noopener noreferrer"><Mail></Mail>: ryanimpey@live.co.uk</a></p>
                    </div>
                </div>
                <p className="subtitle is-6 is-light">Copyright &copy; {this.state.currentYear} | Ryan Impey</p>
            </section>
        );
    }
}

export default Contact;


