import React, { Component } from 'react';
import Skill from './SkillTag';

class Skills extends Component {
    render(){
        return(
            <section className="hero is-light" id="skills">
                <div className="hero-body">
                <div className="container">
                    <div className="columns is-centered">
                        <div className="column is-6">
                        <h1 className="title is-3 is-underlined">Skills</h1>
                        <Skill skill="HTML"></Skill>
                        <Skill skill="CSS"></Skill>
                        <Skill skill="Bulma"></Skill>
                        <Skill skill="Bootstrap"></Skill>
                        <Skill skill="Javascript"></Skill>
                        <Skill skill="ES6"></Skill>
                        <Skill skill="JQuery"></Skill>
                        <Skill skill="JSON"></Skill>
                        <Skill skill="Pug/Jade"></Skill>
                        <Skill skill="Handlebars"></Skill>
                        <Skill skill="NodeJS"></Skill>
                        <Skill skill="Express"></Skill>
                        <Skill skill="Vue"></Skill>
                        <Skill skill="Git (Github/Bitbucket)"></Skill>
                        <Skill skill="OpenStreetMap"></Skill>
                        <Skill skill="Java OpenStreetMap"></Skill>
                        <Skill skill="LeafletJS"></Skill>
                        <Skill skill="C#"></Skill>
                        <Skill skill="C++"></Skill>
                        <Skill skill="OAuth (PassportJS)"></Skill>
                        <Skill skill="NoSQL"></Skill>
                        <Skill skill="MySQL"></Skill>
                        <Skill skill="Twitter API"></Skill>
                        <Skill skill="Steam API"></Skill>
                        <Skill skill="Offerwall Integration"></Skill>
                        <Skill skill="Photoshop"></Skill>
                        </div>
                    </div>
                </div>
                </div>
            </section>
        );
    }
}

export default Skills;


