import React, { Component } from 'react';
import { Twitter, Github } from 'react-feather';

class Hero extends Component {

    constructor(props){
        super(props);
        this.state = {
            heroStyle: {
                background: `linear-gradient(
                    rgba(33,125,187,0.8), 
                    rgba(0, 0, 200, 0.2)
                  ), url(${this.props.background})`,
                backgroundSize: 'cover'
            }
        }
    }

    componentDidMount(){

    }

    render() {
        return(
            <section className="hero is-fullheight is-primary" style={this.state.heroStyle}>
                <div className="hero-body">
                    <div className="container">
                        <div className="columns is-centered">
                            <div className="column is-4 is-titlebox">
                                <h1 className="has-text-centered title is-1 is-roboto-bold">Ryan Impey {this.props.name}</h1>
                                <h2 className="has-text-centered subtitle is-3 has-text-white-ter">Fullstack Developer</h2>
                            </div>
                        </div>
                    </div>
                </div> 
                <div className="hero-foot">
                    <a href="https://twitter.com/wholelifeisary" target="_blank" rel="noopener noreferrer" className="is-feather-icon">
                        <Twitter></Twitter>
                    </a>
                    <a href="https://github.com/ryanimpey" target="_blank" rel="noopener noreferrer" className="is-feather-icon">
                        <Github></Github>
                    </a>
                </div>
            </section>
        );
    }
}

export default Hero;


