import React, { Component } from 'react';

class About extends Component {
    render() {
        return (
            <section className="hero is-medium" id="about">
                <div className="hero-body">
                    <div className="container">
                        <div className="columns">
                            <div className="column is-3">
                                <h1 className="title is-2 is-underline is-roboto-light">About</h1>
                            </div>
                            <div className="column is-7 is-offset-2">
                                <p>
                                    Student and Associate Developer at the University of Lincoln with over 3 years
                                    of experience in building web applications using a variety of tools and libraries for
                                    frontend and backend development.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default About;


